
import { defineComponent } from "vue";
import HeadButton from "@/components/VisitTable/HeadButton.vue";

export default defineComponent({
  components: {
    HeadButton,
  },
  emits: ["onClickGo"],

  methods: {
    onClick: function (type: string) {
      this.$emit("onClickGo", type);
    },
  },
});
